@import './node_modules/frow/frow';

:root {
  --navy : #001F3F;
  --blue : #0074D9;
  --aqua : #7FDBFF;
  --teal : #39CCCC;
  --olive : #3D9970;
  --green : #2ECC40;
  --lime : #01FF70;
  --yellow : #FFDC00;
  --orange : #FF851B;
  --red : #FF4136;
  --fuchsia : #F012BE;
  --purple : #B10DC9;
  --maroon : #85144B;
  --white : #FFFFFF;
  --gray : #AAAAAA;
  --silver : #DDDDDD;
  --black : #111111;
}

* > *{
  font-family: 'Fredoka One', cursive;
}


.isSelected {
  border: 1px solid var(--red);
}

.isToday {
  background-color: var(--blue);
}

.selectedWeek {
  background-color: var(--yellow);
}
